.header{
    display: flex;
    justify-content: start;
    width: 110%;
    align-items: center;
    margin-left: -1rem;
    min-height: 2rem;
    font-size: 2rem;
    background-color: #2baf01;
    color: white;
}
.search_input{
    width: 30rem;
    height: 1.5rem;
    border-style: solid;
    border-color: white;
    border-radius: 1rem;
    margin-right: 2rem;
}
.search_button{
    width: 5rem;
    height: 2rem;
    background-color: white;
    border-color: white;
    border-radius: 1rem;
    margin-right: 0.5rem;
}
.search{
    width: 40rem;
    margin-left:10rem ;
}
