.lists{
    display: flex;
    flex-wrap: wrap;
    padding: 1rem 1rem 1rem 1rem;
}
.details{
    width: 100%;
    text-align: center;
    font-size: 2rem;
    margin-top: 2rem;
}
.anh-sp{
    width: 60%;
    height: 25rem;
}
.button_add{
    height: 3rem;
    width: 16rem;
    font-size: 2rem;
    color: white;
    background-color:#2baf01 ;
    margin-top: 1rem;
    border-style: solid;
    border-radius: 4px;
    border-color: white;
    border-width: 1px;
}
.Login{
    width: 100%;
}
.login-box{
    position: relative;
    display: block;
    align-items: center;
    text-align: center;
    font-size: 1rem;
    
}
.auth{
    width: 20rem;
    height: 2rem;
    border-style: solid;
    border-width: 2px;
    border-radius: 10px;
    margin-bottom: 3rem;
}
.log_button{
    width: 18rem;
    height: 2rem;
    border-style: solid;
    border-width: 1px;
    border-radius: 13px;
    border-color: #2baf01;
    background-color:#2baf01 ;
}
.sucess{
    text-align: center;
    background-color: white;
    color: #2baf01;
    border-style: #2baf01;
    border-radius: 15px;
    width: 25rem;
    position: relative;
    left: 36rem;
    margin-bottom: 2rem;
}